@import url("https://fonts.googleapis.com/css2?family=Courier+Prime:ital,wght@0,700;1,400&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  max-width: 100vw !important;
  height: 100vh;
  font-family: "Courier Prime", monospace;
  background: white;
}

canvas {
  display: block;
}

#container {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: -1;
}

.section {
  min-height: 100vh;
  /* display: flex;
  place-items: center;*/
  color: white;
  padding: 170px;
}

.section h1 {
  width: 100%;
  display: block;
  font-size: 10vw;
}

.section p {
  width: 15vw;
}

.section:nth-child(2) {
  text-align: right;
}
